import React from "react";
import Header from "./commonComponent/header";
import SideBar from "./commonComponent/sideBar";
import { Link, useLocation, useParams } from "react-router-dom";
import { useQuery } from "@tanstack/react-query";
import { bannerDetails } from "../apiServices/home/homeHttpService";

function PromoDetails() {
  const location = useLocation();
  const queryParams = new URLSearchParams(location.search);
  let { id } = useParams();

  const { data: details } = useQuery({
    queryKey: ["bannerDetail", id],
    queryFn: async () => {
      return bannerDetails(id);
    },
    onError: (error) => {
      console.log(error);
    },
    select: (data) => data.results.details[0],
  });

  console.log(details);

  return (
    <>
      <Header />
      <div className="admin_main">
        <SideBar />
        <div className="admin_contentpart">
          <Link
            to={`/admin/promotional-banner?page=${
              queryParams.get("page") || 1
            }`}
            className="comman_btn w-fit"
          >
            <i className="fa fa-arrow-left" />
          </Link>

          <div className="row comman_design mx-0 mt-4">
            <div className="col-12">
              <div className="row comman_table pt-md-3 pb-md-4 pb-3 px-md-3 px-2">
                <div className="col-12">
                  <div className="bg-main rounded-3">
                    <div className="main-banner-wrapper">
                      <div className="row align-items-center justify-content-center">
                        <div className="col-8">
                          <div className="banner-img">
                            <img
                              src={details?.image}
                              alt=""
                              className="w-100 h-100"
                            />
                          </div>
                        </div>
                        <div className="col-4">
                          <div className="main-content-wrapper">
                            <div className>
                              <div className="banner-img-wrapper">
                                <img
                                  src="../../assets/img/logo.png"
                                  alt=""
                                  className="w-100 h-100"
                                />
                              </div>
                              <div className="main-banner-text">
                                <h1 className="fs-4 mt-2 fw-semibold mb-0 text-white">
                                  {details?.bannerName}
                                </h1>
                                <p className="mt-2 leading-1 fw-light mb-0 text-white w-75">
                                  {details?.description}
                                </p>
                              </div>
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default PromoDetails;
